"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";

export default function EventList({ events = [] }) {
  const router = useRouter();

  const [deleteModal, setDeleteModal] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [loading, setLoading] = useState(false);

  /* ================= EDIT ================= */
  const handleEdit = (event) => {
    // example: /events/edit/[identity]
    router.push(`/events/edit/${event.identity}`);
  };

  /* ================= DELETE ================= */
  const handleDeleteClick = (event) => {
    setSelectedEvent(event);
    setDeleteModal(true);
  };

  const handleDeleteConfirm = async () => {
    if (!selectedEvent) return;

    try {
      setLoading(true);

      // üî¥ Replace with real delete API
      // await deleteEventApi(selectedEvent.identity);

      console.log("Deleted Event:", selectedEvent.identity);

      setDeleteModal(false);
      setSelectedEvent(null);
    } catch (err) {
      console.error("Delete failed", err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ padding: 24 }}>
      <h2>My Events</h2>

      {/* ================= TABLE ================= */}
      <table width="100%" border="1" cellPadding="10" style={{ marginTop: 16 }}>
        <thead>
          <tr style={{ background: "#f5f5f5" }}>
            <th>Title</th>
            <th>Mode</th>
            <th>Status</th>
            <th>Organization</th>
            <th>Actions</th>
          </tr>
        </thead>

        <tbody>
          {events.length === 0 && (
            <tr>
              <td colSpan="5" align="center">
                No events found
              </td>
            </tr>
          )}

          {events.map((event) => (
            <tr key={event.identity}>
              <td>{event.title}</td>
              <td>{event.mode}</td>
              <td>
                <span
                  style={{
                    padding: "4px 10px",
                    borderRadius: 12,
                    fontSize: 12,
                    background:
                      event.status === "APPROVED"
                        ? "#d4f8d4"
                        : event.status === "PENDING"
                        ? "#fff3cd"
                        : "#f8d7da",
                  }}
                >
                  {event.status}
                </span>
              </td>
              <td>{event.org?.organizationName}</td>
              <td>
                <button
                  onClick={() => handleEdit(event)}
                  style={{ marginRight: 8 }}
                >
                  ‚úèÔ∏è Edit
                </button>

                <button
                  onClick={() => handleDeleteClick(event)}
                  style={{ color: "red" }}
                >
                  üóë Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* ================= DELETE CONFIRM MODAL ================= */}
      {deleteModal && (
        <div style={overlayStyle}>
          <div style={modalStyle}>
            <h3>Delete Event</h3>
            <p>
              Are you sure you want to delete
              <b> {selectedEvent?.title}</b>?
              <br />
              This action cannot be undone.
            </p>

            <div style={{ marginTop: 20, textAlign: "right" }}>
              <button
                onClick={() => setDeleteModal(false)}
                disabled={loading}
                style={{ marginRight: 10 }}
              >
                Cancel
              </button>

              <button
                onClick={handleDeleteConfirm}
                disabled={loading}
                style={{ background: "red", color: "#fff" }}
              >
                {loading ? "Deleting..." : "Delete"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

/* ================= STYLES ================= */

const overlayStyle = {
  position: "fixed",
  inset: 0,
  background: "rgba(0,0,0,0.5)",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  zIndex: 1000,
};

const modalStyle = {
  background: "#fff",
  padding: 24,
  borderRadius: 8,
  width: 400,
};
